--><html><head></head><body><h1>ArcGISTiledElevationTerrainProvider</h1>
<p><code v-pre="">vc-provider-terrain-arcgis-tiled-elevation</code> 组件用于加载 ArcGISTiledElevation 格式的地形。</p>
<h2>示例</h2>
<h3>加载 ArcGISTiledElevation 格式地形</h3>
<h4>预览</h4>
<doc-preview>
   <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery>
          <vc-provider-imagery-bingmaps url="https://dev.virtualearth.net" bm-key="AgcbDCAOb9zMfquaT4Z-MdHX4AsHUNvs7xgdHefEA5myMHxZk87NTNgdLbG90IE-" map-style="Aerial"></vc-provider-imagery-bingmaps>
        </vc-layer-imagery>
        <vc-provider-terrain-arcgis-tiled-elevation :url="terrainUrl"></vc-provider-terrain-arcgis-tiled-elevation>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
